<script>
import ModalView from './ModalView.vue';

export default {
  name: 'Header',
  components: {
    ModalView,
  },
  data() {
    return {
      showContent: false,
      releasestop: false,
      audioData: null,
      changing: false,
    };
  },
  methods: {
    openModal() {
      this.showContent = true;
      this.$emit('child-opening');
      this.$refs.modalRef.receiveStatus(this.releasestop);
    },
    closeModal(releasestop) {
      this.showContent = false;
      this.releasestop = releasestop;
      this.$emit('child-closing', this.releasestop);
    },
    receiveAudioFromModal(audiodata) {
      this.audioData = audiodata;
      this.$emit('audio-to-main', this.audioData);
    },
    changeReceive() {
      this.$emit('change-serve');
    },
    defaultReceive() {
      this.$emit('default-serve');
    },
    toChanging() {
      this.changing = true;
    },
    toNotChanging() {
      this.changing = false;
    },
  },
};
</script>

